c.")])])])]),_c('div',{staticClass:"tarjeta tarjeta--slyder p-3"},[_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"xxx"}},[_c('div',{staticClass:"col-md-4 mb-4 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/sly4-3.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-md-8"},[_c('h4',{staticClass:"mb-4"},[_vm._v("Desinfección")]),_c('p',{staticClass:"mb-4"},[_vm._v("Se deben definir planes validados de limpieza y desinfección por cada área y equipo, teniendo en cuenta la frecuencia, método, producto y responsable directo de cada área. Se debe hacer una selección de detergentes y desinfectantes, además de tener claros los datos técnicos y precauciones, con disolución adecuada a la hora de desinfectar. Todas las superficies que entren en contacto con los alimentos incluyen herramientas y guantes, que deben limpiarse y desinfectarse de forma frecuente. Las superficies que se usan para fabricar alimentos de baja humedad deben estar secos y en condiciones sanitarias.")])])])]),_c('div',{staticClass:"tarjeta tarjeta--slyder p-3"},[_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"xxx"}},[_c('div',{staticClass:"col-md-4 mb-4 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/sly4-3.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-md-8"},[_c('h4',{staticClass:"mb-4"},[_vm._v("Monitoreo ambiental")]),_c('p',{staticClass:"mb-4"},[_vm._v("Las pruebas para el control ambiental son una manera de calcular la efectividad de los procesos sanitarios: evaluar las superficies y aguas de enjuague bioluminiscencia. Proteínas descompuestas: método de detección colorimétrico (verde, gris, púrpura). Se deben tener claro cuáles son los puntos de monitoreo, los tanques de almacenamiento, las conexiones en sistemas, los cortadores.")])])])])])],1)])]),_vm._m(45),_vm._m(46),_vm._m(47),_vm._m(48),_c('separador'),_vm._m(49),_vm._m(50)