ull"}},[_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Preparación"}},[_c('div',{staticClass:"col-lg-2 col-md-3 col-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/aco2-1.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-lg-8 col-md-12 col-sm-12"},[_c('p',[_vm._v("Hay productos que necesitan ser preparados porque pueden ser cocinados, asados o se les puede agregar algún condimento, dejando esto en responsabilidad de quien exhibe la degustación. Logrando así, la aceptación o rechazo, por parte del cliente. Es importante tener en cuenta que no se puede alterar el sabor original con demasiados condimentos, para no afectar la intención de la degustación.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Ejecución"}},[_c('div',{staticClass:"col-lg-2 col-md-3 col-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/aco2-2.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-lg-8 col-md-12 col-sm-12"},[_c('p',[_vm._v("Es vital que en la creación del plan de degustación se tengan en cuenta las negociaciones necesarias con el establecimiento comercial para ejecutar la actividad de forma adecuada. Por ejemplo, la instalación de un stand o mueble degustador, su tamaño, si se requiere de tomacorriente, que esté cerca del producto en góndola, cuántas personas estarán presentes haciendo la presentación de la degustación, si se necesita refrigeración, si es necesario un espacio en el establecimiento para guardar materiales o productos, etc. ")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Personal"}},[_c('div',{staticClass:"col-lg-2 col-md-3 col-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/aco2-3.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-lg-8 col-md-12 col-sm-12"},[_c('p',[_vm._v("La degustación brinda significativos beneficios